import React, {createRef, FC} from 'react';
import {Fade, Slide} from 'react-awesome-reveal';
import '../styles/EducationCard.scss';

interface School {
    logo: string;
    schoolName: string;
    subHeader: string;
    duration: string;
    desc: string;
    descBullets?: string[];
}

interface EducationCardProps {
    school: School;
}

const EducationCard: FC<EducationCardProps> = ({school}) => {
    const imgRef = createRef<HTMLImageElement>();

    const GetDescBullets: FC<{ descBullets?: string[] }> = ({descBullets}) => {
        return (
            <>
                {descBullets?.map((item, i) => (
                    <li key={i} className="subTitle">
                        {item}
                    </li>
                ))}
            </>
        );
    };

    if (!school.logo) {
        console.error(`Image of ${school.schoolName} is missing in education section`);
    }

    return (
        <div>
            <Fade direction={"left"} duration={1000}>
                <div className="education-card">
                    {school.logo && (
                        <div className="education-card-left">
                            <img
                                crossOrigin="anonymous"
                                ref={imgRef}
                                className="education-roundedimg"
                                src={school.logo}
                                alt={school.schoolName}
                            />
                        </div>
                    )}
                    <div className="education-card-right">
                        <h5 className="education-text-school">{school.schoolName}</h5>
                        <div className="education-text-details">
                            <h5 className="education-text-subHeader">{school.subHeader}</h5>
                            <p className="education-text-duration">{school.duration}</p>
                            <p className="education-text-desc">{school.desc}</p>
                            <ul className="education-text-bullets">
                                <GetDescBullets descBullets={school.descBullets}/>
                            </ul>
                        </div>
                    </div>
                </div>
            </Fade>
            <Slide duration={2000}>
                <div className="education-card-border"></div>
            </Slide>
        </div>
    );
};

export default EducationCard;
